import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, retry } from 'rxjs/operators';

export interface GenericModel {
  errorMessage: string;
  message: string;
  responseString: any;
  result: boolean;
}

export interface CardModel {
  comment_ID: number;
  comment_post_ID: number;
  comment_author: string;
  comment_author_email: string;
  comment_author_url: string;
  comment_author_IP: number;
  comment_date: Date;
  comment_date_gmt: Date;
  comment_content: string;
  comment_karma: number;
  comment_approved: number;
  comment_agent: string;
  comment_type: string;
  comment_parent: number;
  user_id: number;
  star_rating?: number;
  fest_name?: string;
}

@Injectable({
  providedIn: 'root',
})
export class CardService {
  constructor(private http: HttpClient) {}

  getReviewAPIUrl = 'http://wwfapi.worldwidefests.com/public/api/v1/getReviews';

  getCardDetails() {
    return this.http.get<GenericModel>('./assets/card-data.json')
    .pipe(retry(3), map((data) => {
      const tempData: CardModel[] = data.responseString;
      tempData.forEach((card) => {
        const rating = Math.floor(Math.random() * 6);
        card.star_rating = rating;
      });
      return tempData;
    }));
  }
}
