import { BrowserModule } from '@angular/platform-browser';
import { NgModule, DoBootstrap, Injector } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { HttpClientModule } from '@angular/common/http';


import { AppComponent } from './app.component';
import { UICardComponent } from './uicard/uicard.component';
import { LoadSpinnerComponent } from './load-spinner/load-spinner.component';

@NgModule({
  declarations: [
    AppComponent,
    UICardComponent,
    LoadSpinnerComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule
  ],
  bootstrap: [UICardComponent],
  entryComponents: [UICardComponent]
})
export class AppModule implements DoBootstrap {
  constructor(private injector: Injector) {
    const webComponent = createCustomElement(UICardComponent, {injector});
    customElements.define('ui-card', webComponent);
  }
  ngDoBootstrap(): void {}

}
